.listadoPrecios .buscador {
  display: flex;
  justify-content: space-evenly;
}
.listadoPrecios .listar {
  margin-top: 1.4rem;
}
.listadoPrecios .listar table {
  border-collapse: collapse;
  width: 100%;
}
.listadoPrecios .listar table caption {
  font-weight: bold;
  font-size: 2.4rem;
}
.listadoPrecios .listar table .tbody tr {
  border-bottom: 0.1rem solid black;
}